.profile-h2{
    margin-left: 4rem;
    margin-top: 2rem;
    margin-bottom: 1rem;
    font-size: 2rem;
    font-family: "inter";
    font-weight: 500;
    display:flex;
    flex-direction: row;
    justify-content: space-between;

}

.profile h1{
    font-size: 1.5rem;
    font-family: "inter";
    font-weight: 400;
    margin-left: 2rem;
}

.profile-scaleBox{
    
    box-sizing: border-box;
    border-radius: 25px;
    background-color:lightgrey;
    padding: 1rem;
    margin-left: 2.5rem;
    width: 45rem;
}
.profile-scaleBox3{
    box-sizing: border-box;
    border-radius: 25px;
    background-color:lightgrey;
    padding: 1rem;
    margin-left: 2.5rem;
    width: 45rem;
    height: 25.6rem;
    overflow: auto;
}

::-webkit-scrollbar {    
    width: 25px;
}
::-webkit-scrollbar-thumb {
    background: grey;
    border-radius: 25px;

  }



.profile-scaleBox2{
    box-sizing: border-box;
    border-radius: 25px;
    background-color:#d3d3d3;
    padding: 1.5rem;
    margin-left: 2.5rem;
    margin-right: 2.5rem;
    overflow-y: auto;
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
    row-gap: 2rem;
    min-height: 22rem;
    max-height: 43rem;
    justify-content: flex-start; /*not doin anythin */
    
}

.profile-rightDiv{
    align-content: flex-start;
    justify-content: flex-start;
}

.profile-bigDiv {
    display: flex;
    gap: 5rem;
    font-size: 1.5rem;
}

.profile-boxDiv {
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    border: 5px solid yellow
}

.profile h3{
    font-size: 0.6rem;
    display: inline;
    font-family: "inter";
    margin-left: 1rem;  
    font-family: "inter";
    font-weight: 400;

  

}
.profile h4{
    text-align: center;
    font-family: "inter";
    font-size: 1rem;
    font-weight: 400;

}
.profile-image{
    background-color:lightgrey;
    height: 10rem;
    width: 14rem;
    margin-left: 1rem;
}

.profile-button{
    -webkit-transition:  -webkit-transform .2s ease-out;
    -moz-transition: -moz-transform .2s ease-out;
    -o-transition: -o-transform .2s ease-out;
    -ms-transition: -ms-transform .2s ease-out; 
    transition: transform .2s ease-out; 
    margin-left: 14.5rem;
    margin-bottom: -1.25rem;
}

.profile-button:hover{
    -webkit-transform:scale(1.5);
    -moz-transform:scale(1.5);
    -o-transform:scale(1.5);
    transform:scale(1.5);
    cursor:pointer;
}

.profile-modal-title{
    font-size: 2rem;
    font-family: "inter";
    font-weight: 500;
}
